import axios from 'axios';

// Funo para obter e exibir os Digimons com filtro por ordem alfabtica
// Caso queria mudar a ordem para ser inversa a ordem alfabtica mudar o parmetro para desc 
export default async function getDigimons(order = 'asc') {
    try {
        // Faz uma requisio  API para obter os dados dos Digimons
        const response = await axios.get('https://digimon-api.vercel.app/api/digimon');
        const digimons = response.data; // Armazena a resposta da API em uma varivel

        // Ordena os Digimons por nome em ordem alfabtica
        digimons.sort((a, b) => (order === 'asc' ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name)));

        // Obtm a lista HTML onde os Digimons sero exibidos
        const digimonList = document.getElementById('digimon-list');
        digimonList.innerHTML = ''; // Limpa a lista antes de adicionar os Digimons

        // Itera sobre os Digimons ordenados e cria elementos HTML para cada um
        digimons.forEach(digimon => {
            const digimonCard = document.createElement('div');
            digimonCard.classList.add('digimon-card'); // Adiciona a classe 'digimon-card' para estilizao

            // Cria um elemento de ttulo (<h2>) para o nome do Digimon e adiciona ao carto
            const digimonName = document.createElement('h2');
            digimonName.textContent = digimon.name;
            digimonCard.appendChild(digimonName);

            // Cria um elemento de pargrafo (<p>) para o nvel do Digimon e adiciona ao carto
            const digimonLevel = document.createElement('p');
            digimonLevel.textContent = `Nvel: ${digimon.level}`;
            digimonCard.appendChild(digimonLevel);

            // Cria um elemento de imagem (<img>) para a imagem do Digimon e adiciona ao carto
            const digimonImage = document.createElement('img');
            digimonImage.src = digimon.img;
            digimonImage.alt = digimon.name;
            digimonCard.appendChild(digimonImage);

            // Adiciona o carto com as informaes do Digimon  lista de Digimons
            digimonList.appendChild(digimonCard);
        });
    } catch (error) {
        // Captura e imprime qualquer erro que ocorra durante a requisio  API
        console.error('Error fetching digimons:', error);
    }
}
