export default function filtrarPorNome() {
    const inputFiltrar = document.querySelector('.filtrarNome');
    const btnFiltrar = document.querySelector('.btn-filtrar-nome');

    btnFiltrar.addEventListener('click', () => {
    const nomeFiltrar = inputFiltrar.value.toLowerCase(); // Obtm o valor do input e converte para minsculas
    
    const digimonCards = document.querySelectorAll('.digimon-card'); // Obtm todos os cartes de Digimon               
        
        digimonCards.forEach(card => {
            const nomeDigimon = card.querySelector('h2').textContent.toLowerCase();

            // Verifica se o nome do Digimon comea com o filtro inserido pelo usurio
            if (nomeDigimon.startsWith(nomeFiltrar)) {
                card.style.display = 'block';
            } else {
                card.style.display = 'none';
            }
        });
    });
}
