export default function filtrarDigimons() {
    const inputFiltrar = document.querySelector('.filtrar');
    const btnFiltrar = document.querySelector('.btn-filtrar');

    btnFiltrar.addEventListener('click', () => {
        const nivelFiltrar = inputFiltrar.value.toLowerCase(); // Obtm o valor do input e converte para minsculas

        const digimonCards = document.querySelectorAll('.digimon-card'); // Obtm todos os cartes de Digimon

        // Itera sobre os cartes de Digimon e verifica se o nvel corresponde ao valor filtrado
        digimonCards.forEach(card => {
            const nivelDigimon = card.querySelector('p').textContent.toLowerCase(); // Obtm o nvel do Digimon

            // Se o nvel do Digimon corresponder ao nvel filtrado, exibe o carto, caso contrrio, oculta o carto
            if (nivelDigimon.includes(nivelFiltrar)) {
                card.style.display = 'block';
            } else {
                card.style.display = 'none';
            }
        });
    });
}
